<script>
  import {
    contributionData,
    allocatedContributions,
  } from "../stores/contributionsStore";
  import { infoFormData } from "../stores//infoStore";
  import { confirmPopUpState } from "../stores/infoStore";
  import { headSteps, incrementStep, stepCounter } from "../stores/store";
  import { beforeUpdate } from "svelte";
  import {fade} from 'svelte/transition'
  import StepContribution_ico from "../../public/images/StepContribution_ico.svelte";
  import StepPlan_ico from "../../public/images/StepPlan_ico.svelte";
  import StepLegal_ico from "../../public/images/StepLegal_ico.svelte";
  import LegalCheckbox_ico from "../../public/images/LegalCheckbox_ico.svelte";
  import StepInformation_ico from "../../public/images/StepInformation_ico.svelte";
  import OpenAcount_ico from "../../public/images/OpenAcount_ico.svelte";
  import { priceConvertation } from "../../src/functions/priceConvertation";

  let changeCounter = 0;

  let currentDate = new Date();
  let currentYear = currentDate.getFullYear();
  let currentDay = currentDate.getDate();

  $: currentYear;

  if ($contributionData.nextPaymentMonth === "January") {
    if ($contributionData.period === "Monthly") {
      currentYear = currentYear + 1;
    } else if ($contributionData.period === "Bi-Monthly" && currentDay >= 15) {
      currentYear = currentYear + 1;
    }
  }
  let confirmAllData = () => {
    $confirmPopUpState = false;
    $headSteps.fifthStep = true;
    if (changeCounter === 0) {
      incrementStep();
      changeCounter += 1;
    }
  };
  let closePopUp = () => {
    $confirmPopUpState = false;
  };

   let changeStep = (stepNum) => {
    $confirmPopUpState = false;
    $stepCounter = stepNum
   } 
  let safePrice = 0,
    adventurePrice = 0,
    founderPrice = 0;

  beforeUpdate(() => {
    safePrice =
      ($allocatedContributions.safe * $contributionData.monthlyValue) / 100;
    adventurePrice =
      ($allocatedContributions.adventure * $contributionData.monthlyValue) /
      100;
    founderPrice =
      ($allocatedContributions.founder * $contributionData.monthlyValue) / 100;
  });
</script>

<div class="pop__up" in:fade>
  <div class="pop__up__wrapper">
    <div class="pop__up__head">
      <div class="pop__up_head__text">
        {$infoFormData.userName} LET'S MAKE SURE WE'VE GOT EVERYTHING RIGHT!
      </div>
      <div class="pop__up__close" on:click={closePopUp}><span /></div>
    </div>
    <div class="pop__up__body">
      <h3>Final Review</h3>
      <div class="pop__up__items">
        <div class="pop__up__item">
          <div class="item__head">
            <div class="step">
              <div class="step__icon">
                <StepContribution_ico />
              </div>
              <div class="step__name">Contribution</div>
            </div>
            <div class="change__btn"on:click={()=>{changeStep(1)}}>Change</div>
          </div>
          <div class="item__body">
            <div class="text">
              {$contributionData.currencySymbol}{priceConvertation($contributionData.monthlyValue)}
              USD per Month Starting {$contributionData.nextPaymentDay}th {$contributionData.nextPaymentMonth}
              {currentYear}
            </div>
          </div>
        </div>
        <div class="pop__up__item">
          <div class="item__head">
            <div class="step">
              <div class="step__icon">
                <StepPlan_ico />
              </div>
              <div class="step__name">Plan</div>
            </div>
            <div class="change__btn"on:click={()=>{changeStep(2)}}>Change</div>
          </div>
          <div class="item__body">
            <div class="item__plan save">
              <div class="name">Safe</div>
              <div class="persentage">{$allocatedContributions.safe}%</div>
              <div class="money">
                {$contributionData.currencySymbol}{priceConvertation(Math.round(safePrice))}
              </div>
            </div>
            <div class="item__plan adventure">
              <div class="name">Adventure</div>
              <div class="persentage">{$allocatedContributions.adventure}%</div>
              <div class="money">
                {$contributionData.currencySymbol}{priceConvertation(Math.round(adventurePrice))}
              </div>
            </div>
            <div class="item__plan founder">
              <div class="name">Founder</div>
              <div class="persentage">{$allocatedContributions.founder}%</div>
              <div class="money">
                {$contributionData.currencySymbol}{priceConvertation(Math.round(founderPrice))}
              </div>
            </div>
          </div>
        </div>
        <div class="pop__up__item">
          <div class="item__head">
            <div class="step">
              <div class="step__icon">
                <StepLegal_ico />       
              </div>
              <div class="step__name">Legal</div>
            </div>      
          </div>
          <div class="item__body">
            <div class="legal__item">
              <LegalCheckbox_ico />
              <div class="text">
                You agreed to all Terms & Conditions, Contract Agreement,
                Privacy & Cookie
              </div>
            </div>
          </div>
        </div>
        <div class="pop__up__item">
          <div class="item__head">
            <div class="step">
              <div class="step__icon">
                <StepInformation_ico />
              </div>
              <div class="step__name">Information</div>
            </div>
            <div class="change__btn" on:click={()=>{changeStep(4)}}>Change</div>
          </div>
          <div class="item__body">
            <div class="text">N: {$infoFormData.userName}</div>
            <div class="text">P: {$infoFormData.phone}</div>
            <div class="text">E: {$infoFormData.email}</div>
          </div>
        </div>
      </div>
      <button class="submt__btn" on:click={confirmAllData}><span>Open Account</span><OpenAcount_ico /></button>
    </div>
  </div>
</div>

<style>
  .pop__up {
    position: absolute;
    top: 0;
    z-index: 10;
    width: 100%;
    height: auto;
    padding: 0;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .pop__up__wrapper {
    border-radius: 10px;
    background: white;
    box-shadow: 0px 25px 35px rgba(0, 0, 0, 0.15),
      inset 0px 0px 35px rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(5px);
    margin: 0 auto;
    max-width: 999px;
    font-size: 14px;
    line-height: 21px;
    margin: 120px auto;
  }
  .pop__up__head {
    width: 100%;
    padding: 35px;
    /* dark-green */
    background: linear-gradient(249.52deg, #0c9532 0%, #17cc49 100%);
    position: relative;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }
  .pop__up_head__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: #ffffff;
    text-transform: uppercase;
    text-align: center;
  }
  .pop__up__close {
    width: 24px;
    height: 24px;
    position: absolute;
    right: 24px;
    top: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3;
    cursor: pointer;
  }
  .pop__up__close span {
    position: relative;
    width: 21px;
    height: 1.9px;
    background: white;
    transform: rotate(45deg);
  }
  .pop__up__close span:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 21px;
    height: 1.9px;
    background: inherit;
    transform: rotate(90deg);
  }

  .pop__up__body {
    width: 100%;
    max-width: 539px;
    padding: 32px 20px 100px 20px;
    margin: 0 auto;
  }
  h3 {
    font-weight: 500;
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    margin-bottom: 34px;
  }
  .pop__up__items {
  }
  .pop__up__item {
    border: 1px solid #e4e4e4;
    border-radius: 4px;
    overflow: hidden;
    margin-bottom: 24px;
  }
  .pop__up__item:last-child {
    margin-bottom: 34px;
  }
  .item__head {
    background: #e4e4e4;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .step {
    display: flex;
    align-items: center;
    padding: 13px 20px;
  }
  .step__icon {
    margin-right: 6px;
    width: 24px;
    height: 24px;
  }
  .step__name {
    font-weight: 500;

    color: #3e6b2c;
  }
  .change__btn {
    width: 121px;
    height: 100%;
    font-weight: 500;

    color: #0085ff;
    text-align: center;
    cursor: pointer;
  }
  .change__btn:hover {
    color: #3e6b2c;
  }
  .item__body {
    padding: 14px 20px 24px 20px;
  }
  .text {
    font-weight: 400;
    color: #000000;
  }
  .item__plan {
    border-radius: 4px;
    padding: 10px 24px;
    color: #0085ff;
    border: 1px solid #0085ff;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    margin-bottom: 8px;
  }
  .item__plan:last-child {
    margin: 0;
  }
  .save {
  }
  .name {
  }
  .persentage {
    text-align: center;
  }
  .money {
    text-align: end;
  }
  .adventure {
    color: #6cc800;
    border: 1px solid #6cc800;
  }
  .founder {
    color: #5e0bc9;
    border: 1px solid #5e0bc9;
  }
  .legal__item {
    display: flex;
  }
  .legal__item .text {
    max-width: 336px;
    margin-left: 12px;
  }
  .submt__btn {
    padding: 21px 32px;
    background: #006eff;
    border-radius: 10px;
    color: #fff;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #ffffff;
    border: none;
    margin: 0 auto;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  .submt__btn:hover {
    background: #006effd8;
  }
  .submt__btn:active {
    background: #006eff;
  }

  @media only screen and (max-width: 768px) {
    .pop__up__wrapper {
      margin: 0;
      border-radius: 0;
    }
    .pop__up__head {
      border-radius: 0;
    }
    .pop__up__close {
      width: 24px;
      height: 24px;
      position: absolute;
      right: 15px;
      top: 15px;
    }
  }
</style>
